"use client"

import { useEffect, useRef } from "react"
import cytoscape from "cytoscape"
import type { NodeData } from "@/types/node_types"

// Sample graph data
const graphData = {
  nodes: [
    { data: { id: "1", label: "Node 1", description: "This is node 1", color: "#ff5733" } },
    { data: { id: "2", label: "Node 2", description: "This is node 2", color: "#33ff57" } },
    { data: { id: "3", label: "Node 3", description: "This is node 3", color: "#3357ff" } },
    { data: { id: "4", label: "Node 4", description: "This is node 4", color: "#f3ff33" } },
    { data: { id: "5", label: "Node 5", description: "This is node 5", color: "#ff33f5" } },
  ],
  edges: [
    { data: { id: "e1", source: "1", target: "2" } },
    { data: { id: "e2", source: "1", target: "3" } },
    { data: { id: "e3", source: "2", target: "4" } },
    { data: { id: "e4", source: "3", target: "5" } },
    { data: { id: "e5", source: "4", target: "5" } },
  ],
}

//関数型のアノテーションを返す。通常はname: stringみたいに書くが、ここでは、関数が何の引数を受け取って、何を返すかを明示的に示している。
//この場合、引数はonNodeClickという関数で、引数は一つ。NodeData型で、そして何も返さない。
interface CytoscapeGraphProps {
  onNodeClick: (nodeData: NodeData) => void
}



export default function CytoscapeGraph({ onNodeClick }: CytoscapeGraphProps) {
  const containerRef = useRef<HTMLDivElement>(null)
  const cyRef = useRef<cytoscape.Core | null>(null)

  useEffect(() => {
    if (!containerRef.current) return

    // Initialize cytoscape
    const cy = cytoscape({
      container: containerRef.current,
      elements: [...graphData.nodes, ...graphData.edges],
      style: [
        {
          selector: "node",
          style: {
            "background-color": "data(color)",
            label: "data(label)",
            color: "#fff",
            "text-outline-color": "#000",
            "text-outline-width": 1,
            "font-size": 14,
            width: 40,
            height: 40,
          },
        },
        {
          selector: "edge",
          style: {
            width: 2,
            "line-color": "#ccc",
            "curve-style": "bezier",
          },
        },
      ],
      layout: {
        name: "circle",
      },
    })

    // Add click event listener to nodes
    cy.on("tap", "node", (event) => {
      const node = event.target
      const nodeData: NodeData = {
        id: node.id(),
        label: node.data("label"),
        description: node.data("description"),
        color: node.data("color"),
      }
      onNodeClick(nodeData)
    })

    cyRef.current = cy

    // Clean up
    return () => {
      if (cyRef.current) {
        cyRef.current.destroy()
      }
    }
  }, [onNodeClick])

  return <div ref={containerRef} className="w-full h-full" />
}

